'use client';

import { useEffect, useState } from 'react';
import { useSession } from 'next-auth/react';
import { useAppContext } from '@/context/AppContext';
import { apiClient } from '@/lib/api';
import { User } from '@/types';
import db from '@/lib/db';

export const useSyncNextAuthSession = () => {
  const { data: session, status } = useSession();
  const { user, setUser, setToken, logout, fetchUserProfile, addToCart } = useAppContext();
  const [isProcessing, setIsProcessing] = useState(false);

  useEffect(() => {
    // Track if user is from NextAuth (Google) or from our auth (email/password)
    const isGoogleUser = !!session?.user;
    const isEmailUser = !!user?.name && !session?.user;  // Has user data but no session = email auth

    if (isProcessing) {
      return;
    }

    if (isGoogleUser && session?.user && session.user?.email) {
      const email = session.user.email;
      
      // Check if this is a new login (user was not previously authenticated)
      const wasPreviouslyAuthenticated = !!user;

      // Sync user to database
      (async () => {
        setIsProcessing(true);
        try {
          // Query for existing user by email OR google_id
          const existingUserQuery = await db.query(
            'SELECT id, email, name, role, avatar, google_id FROM users WHERE email = $1',
            [email]
          );
          
          let dbUser: User | null = null;

          if (existingUserQuery.rows.length > 0) {
            // User already exists in database
            dbUser = existingUserQuery.rows[0];

            // Update google_id if not set
            if (!dbUser.google_id && (session.user as any)?.id) {
              const { data, error } = await db
                .from('users')
                .update({ google_id: (session.user as any)?.id })
                .eq('email', email)
                .select()
                .single();
              
              if (!error && data) {
                dbUser.google_id = data.google_id;
              }
            }
          } else {
            // User doesn't exist - create new entry
            const newUserData = {
              email: email,
              name: session.user?.name || session.user?.email?.split('@')[0] || 'User',
              google_id: (session.user as any)?.id || '',
              avatar: session.user?.image || null,
              role: 'user',
              created_at: new Date().toISOString(),
              updated_at: new Date().toISOString()
            };
            
            const { data, error } = await db
              .from('users')
              .insert([newUserData])
              .select()
              .single();
            
            if (error) {
              console.error('Error creating user in database:', error);
            } else if (data) {
              dbUser = data;
              console.log('✅ New user created in database:', dbUser);
            }
          }

          if (dbUser) {
            // Update the app context user
            const userObject: User = {
              id: dbUser.id,
              email: dbUser.email,
              name: dbUser.name,
              role: dbUser.role,
              avatar: dbUser.avatar || undefined,
              googleId: dbUser.google_id || undefined,
              created_at: dbUser.created_at
            };

            // Only update context if the user has changed to avoid render loops
            if (!user || user.id !== userObject.id || user.googleId !== userObject.googleId) {
              setUser(userObject);
              setToken('nextauth-session');
              console.log('✅ User synced to context and database:', userObject);
            }

            // Fetch user profile and handle pending cart actions
            if (user?.id !== dbUser.id) {
              await fetchUserProfile();
              
              const pendingCartAction = localStorage.getItem('pendingCartAction');
              if (pendingCartAction && !wasPreviouslyAuthenticated) {
                try {
                  const { product, quantity } = JSON.parse(pendingCartAction);
                  if (product && quantity) {
                    await addToCart(product, quantity);
                  }
                } catch (error) {
                  console.error('Error processing pending cart action:', error);
                } finally {
                  localStorage.removeItem('pendingCartAction');
                }
              }
            }
          }
        } catch (error) {
          console.error('Error syncing NextAuth session:', error);
        } finally {
          setIsProcessing(false);
        }
      })();

    } else if (isEmailUser) {
      // EMAIL AUTH: User is logged in via email/password - don't touch!
      // Do nothing - let the email auth handle everything
    } else if (!isGoogleUser && !isEmailUser) {
      // NOT AUTHENTICATED: Both NextAuth and email auth show no user
      if (status === 'unauthenticated' && user) {
        logout();
      }
    }
  }, [status, session, setUser, setToken, logout, fetchUserProfile, user, addToCart, isProcessing]);
};